// Clear Padding and Margin
%clear-gutter {
    margin: 0;
    padding: 0;
}

// Removes the list style
%list-none {
    list-style-type: none;
}

// Transparent BG for Background Images
%bg-transparent {
    content: '';
    background-color: brand(.75);
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
    z-index: -1;
}

// Header Text
%heading {
    @extend .font-face-serif;
}

%headingSecondary {
    @extend .font-face-serif-secondary;
}

// Display
%display {
    @extend .font-face-serif-secondary;
    text-transform: uppercase;
}
